html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
  margin: 0px;
}
body{
  background-color: #f2f2f2;
}
#root {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: 'Nunito', sans-serif;
  max-height: 100vh;
}

#root.dark {
    background-color: #1c1c1c;
}




footer{
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content:flex-start;
    max-width: 1200px;
    width: 90vw;
    padding: 30px 0px 20px 0px;
    position: fixed;
    bottom: 30px;
}

footer a{
    display: block;
    width: 60px;
    height: 60px;
    border-radius: 88px;
    background: linear-gradient(145deg, #ffffff, #dadada);
    box-shadow:  8px 8px 16px #cecece,
                 -8px -8px 16px #ffffff;
    padding: 15px;
    cursor:pointer;
}

footer a:hover {
    background: linear-gradient(145deg, #dadada, #ffffff);
    box-shadow:  8px 8px 16px #cecece,
                 -8px -8px 16px #ffffff;
}


@media (max-width: 1000px) {
    footer {
        bottom: 0px;
    }
}
header {
    margin-top: 40px;
    padding-left: 20px;
    border-radius: 14px;
    background: var(--main-background-color);
    position:-webkit-sticky;
    position:sticky;
    max-width: 1200px;
    width: 90vw;
    height: 100px;
    top: 20px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    border-radius: 11px;
    background: #f2f2f2;
    box-shadow:  15px 15px 31px #cecece,
             -15px -15px 31px #ffffff;
             
    text-shadow: 1px 1px 5px rgb(255, 255, 204);
}

header h1{
    color:  var(--main-font-color);
    text-shadow: 1px 1px 5px rgb(255, 255, 204);
}

header.dark{
    background: #1c1c1c;
    box-shadow:  30px 30px 71px #0d0d0d,
             -30px -30px 71px #2b2b2b;
             color: #cecece;
             
    text-shadow: 1px 1px 5px rgb(245, 131, 131);
}

.screen {
    width: calc(100% - 310px);
    margin-top: 10px;
    height: calc(100% - 20px);
    background-color: white;
    border-radius: 11px;
    background: #f2f2f2;
    box-shadow: inset 5px 5px 10px #dfdfdf,
        inset -5px -5px 10px #ffffff;
    float: left;
    overflow-y: scroll;
    
}

@media (max-width: 1000px) {
    .screen {
        float: none;
        width: calc(100%);
        height: calc(100% - 140px);
    }
}

.about {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.about article{
    width: 100%;
}

.about__heading {
    text-align: center;
    font-size: 24px;
    font-weight: 800;
    margin: 30px;
    padding: 20px 0;
    background: radial-gradient(circle, rgba(236,236,236,1) 35%, rgba(242,242,242,1) 50%);
}

.about__general {
    
}


.about__skills_container {
    width: 100%;
    padding: 0 5%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.about__skills_container img {
    width: 90px
}


.portfolio_container {
    padding: 30px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    grid-gap: 30px;
    gap: 30px;
    height: auto;
    padding-bottom: 100px;
}

.portfolio_category_container {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    grid-gap: 20px;
    gap: 20px;
    margin: 40px auto 20px;
    justify-content: space-evenly;
}

.portfolio_menu_item summary {
    text-align: center;
    padding-bottom: 10px;
    padding: 20px;
    border-radius: 9px;
    background: #f2f2f2;
    box-shadow: 19px 19px 37px #cecece,
        -19px -19px 37px #ffffff;
    font-size: larger;
}

.portfolio_submenu_item{
    text-align: center;
    width: 100%;
    text-indent:0;
    margin-top: 30px;
    font-weight: lighter;
}

.portfolio_fm_explanation {
    margin: 30px;
    text-align: center;
}

.portfolio_fm_explanation a{
    font-weight: bolder;
    cursor: pointer;
    text-decoration: none;
    color: black;
    
}

.portfolio_fm_explanation a:hover{
    text-decoration:underline;
    -webkit-transform: scale(2);
            transform: scale(2);
}
.portfolio_card {
    height: 189px;
    width: 252px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    box-shadow: 5px 5px 10px #e1e1e1,
        -5px -5px 10px #ffffff;
    border-radius: 7px;
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
}

.portfolio_card img {
    width: 90%;
    height: 90%;
    object-fit: cover;
    border: 5px double #f2f2f2;
    border-radius: 5px;
    transition: .5s;
}

.portfolio_card a {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    text-decoration: none;
    cursor: pointer;
    color: black;
    text-align: center;
}

.portfolio_card:hover {
    transition: -webkit-transform 0.7s;
    transition: transform 0.7s;
    transition: transform 0.7s, -webkit-transform 0.7s;
    -webkit-transform: scale3d(1.05, 1.05, 1.05);
    transform: scale3d(1.05, 1.05, 1.05);
}

.portfolio_card .portfolio_card_details {
    box-sizing: border-box;
    margin: 0;
    position: absolute;
    padding: 7px;
    top: 0px;
    width: 252px;
    height: 189px;
    backdrop-filter: blur(8px) saturate(94%);
    -webkit-backdrop-filter: blur(8px) saturate(94%);
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 12px;
    border: 1px solid rgba(255, 255, 255, 0.125);
    transition: 0.7s;
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
    -webkit-transform: perspective(1000px) rotateX(+90deg);
            transform: perspective(1000px) rotateX(+90deg);
}
.portfolio_card:hover .portfolio_card_details {
    -webkit-transform: perspective(1000px) rotateX(0deg);
            transform: perspective(1000px) rotateX(0deg);
}


.portfolio_card_details_name {
    font-size: larger;
    word-break:normal;
}
.portfolio_card_details_langs {
    font-size: smaller;
}
.portfolio_card_details_click {
    font-size: 12px;
}
.portfolio_card_details_name {
}



