main {
    max-width: 1200px;
    width: 90vw;
    height: calc(100vh - 190px);
    z-index: 2;
}

@media (max-width: 1000px) {
    main {
        display: flex;
        flex-direction: column-reverse;
        align-items: center;
        justify-content: flex-end;
    }
}
aside {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

aside form{
    display: flex;
    flex-direction: column;
    padding-left: 15px;
    padding-top: 40px;
}

aside label{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 180px;
    height: 50px;
    margin-bottom: 10px;
    border-radius: 5px;
    border: 1px #dddddd solid;
    background: #f2f2f2;
    box-shadow:  20px 20px 60px #cecece,
    -20px -20px 60px #ffffff;
}

aside label:hover{
    background: linear-gradient(145deg, #dadada, #ffffff);
    box-shadow:  20px 20px 60px #cecece,
             -20px -20px 60px #ffffff;
}

aside input {
    visibility: hidden;
}

@media (max-width: 1000px) {
    aside form{
        padding-top: 10px;
        flex-direction: row;
    }
}
